import { getGlobalData } from '@/api';
import * as types from '@/store/mutation-types';


/**
 * Action: Получение глобальных данных с https://coinmarketcap.com/
 *
 * @param {string|undefined} - Короткое название валюты
 * @since Version 1.0.0
 */
const loadGlobalData = async ({ commit, rootGetters }) => {
  commit(types.GET_GLOBAL_DATA_ASYNC.PENDING);
  const currenceShortName = rootGetters['currencies/activeCurrency'];
  try {
    const res = currenceShortName ? await getGlobalData(currenceShortName) : await getGlobalData();
    commit(types.GET_GLOBAL_DATA_ASYNC.SUCCESS, {
      globalData: res.data,
      activeCurrency: currenceShortName,
    });
  } catch (error) {
    commit(types.GET_GLOBAL_DATA_ASYNC.FAILURE, error);
  }
};


export default {
  loadGlobalData,
};
