<script>
/**
 * Component: Таблица токенов
 *
 * @version 1.0.0
 */

import { mapGetters, mapActions } from 'vuex';

// Components
import PieTimer from '@/components/PieTimer.vue';
import TokenIcon from '@/components/TokenIcon.vue';
import SSelect from '@/components/SSelect.vue';

// Containers
import TheTokenPopup from '@/containers/TheTokenPopup.vue';


export default {
  name: 'the-token-list',

  data() {
    return {
      isTokenPopupShow: false,
      tokenFilter: 'all',
      selectedCategoryIndex: 0,
      selectedCurrencyIndex: 0,

      /**
       * Код токена по котором клиунули в таблице
       */
      clickedTokenCode: null,

      /**
       * Строка поиска по названиям и коротким названиям токенов
       */
      searchString: '',

      categoryValues: [
        'All',
        'New',
        'Top gainers',
        'Top loosers',
      ],
    };
  },

  computed: {
    filteredTokens() {
      const searchToken = this.searchString.toLowerCase();

      return this.searchString
        ? this.tokens.filter(c => c.name.toLowerCase().includes(searchToken) || c.code.toLowerCase().includes(searchToken))
        : this.tokens;
    },

    currenciesArray() {
      return Object.values(this.currencies).map(item => item.shortName);
    },

    ...mapGetters({
      tokens: 'tokens/tokens',
      currencies: 'currencies/currencies',
      activeCurrency: 'currencies/activeCurrency',
    }),
  },

  created() {
    const indexOfActiveCurrency = Object.keys(this.currencies).findIndex(item => item === this.activeCurrency);
    this.selectedCurrencyIndex = indexOfActiveCurrency === -1 ? 0 : indexOfActiveCurrency;
  },

  methods: {
    /**
     * Отображеие попапа с информацией по токену
     */
    toggleTokenPopupShow(code) {
      if (code && !this.isTokenPopupShow) {
        this.clickedTokenCode = code;
      }
      this.isTokenPopupShow = !this.isTokenPopupShow;
    },

    setTokenFilter(tokenFilter) {
      this.tokenFilter = tokenFilter;
    },

    onSelectCategory(index) {
      this.selectedCategoryIndex = index;
    },

    /**
     * Выбор валюты
     */
    async onSelectCurrency(index) {
      await this.setActiveCurrency(this.currenciesArray[index]);
      this.selectedCurrencyIndex = index;
    },

    ...mapActions({
      setActiveCurrency: 'currencies/setActiveCurrency',
    }),
  },

  components: {
    PieTimer,
    SSelect,
    TheTokenPopup,
    TokenIcon,
  },
};
</script>

