import lstore from '@/util/localStorage';
import log from '@/util/logger';
import { getTokens } from '@/api';
import * as types from '@/store/mutation-types';
import { KEY_TOKENS } from '@/util/constants';

/**
 * Action: Получение данных о доступных валютах с flyp.me
 *
 * @since Version 1.0.0
 */
const loadTokens = async ({ commit }) => {
  commit(types.GET_TOKENS_ASYNC.PENDING);
  try {
    const res = await getTokens();
    commit(types.GET_TOKENS_ASYNC.SUCCESS, res);
  } catch (error) {
    log.error(`flyp.com tokens an: ${error}`, {
      module: 'tokens',
      layer: 'flyp.me',
    });
    commit(types.GET_TOKENS_ASYNC.FAILURE);
  }
};


/**
 * Action: Загрузка данных о валютах из локал стореджа
 *
 * @since Version 1.0.0
 */
const loadTokensFromCash = ({ commit, dispatch }) => {
  const tokens = lstore.getValue(KEY_TOKENS);
  if (tokens) {
    commit(types.SET_TOKENS, tokens);
  } else {
    dispatch('loadTokens');
  }
};

export default {
  loadTokens,
  loadTokensFromCash,
};
