import axios from 'axios';
// import { pick } from 'lodash';


/**
 * Получение списка списк поддерживаемых токенов
 *
 * @see [doc](https://flyp.me/api)
 * @since Version 1.0.0
 */
export const getTokens = async () => {
  const { data } = await axios.get('https://flyp.me/api/v1/currencies');
  return data;
};


/**
 * Get exchange rates
 *
 * @see [doc](https://flyp.me/api)
 * @since Version 1.0.0
 */
export const getExchangeRates = async () => {
  const { data } = await axios.get('https://flyp.me/api/v1/data/exchange_rates');
  return data;
};


/**
 * Get max and min limits
 *
 * @param {string} fromCurrency - Короткое название валюты которую меняем
 * @param {string} toCurrency - Короткое название валюты на которую производится обмен
 *
 * @see [doc](https://flyp.me/api)
 * @since Version 1.0.0
 */
export const getExchangeLimits = async (fromCurrency, toCurrency) => {
  const { data } = await axios.get(`https://flyp.me/api/v1/order/limits/${fromCurrency}/${toCurrency}`);
  return data;
};


/**
 * Global Data from https://coinmarketcap.com
 *
 * @param {string | undefined} - Короткое название валюты
 *
 * @returns {objec}
 *
 * @see [doc](https://coinmarketcap.com/api/#endpoint_global_data)
 * @since Version 1.0.0
 */
export const getGlobalData = async (currencyShortName) => {
  const requestURL = currencyShortName ? `https://api.coinmarketcap.com/v2/global/?convert=${currencyShortName}` : 'https://api.coinmarketcap.com/v2/global/';
  const { data } = await axios.get(requestURL);
  return data;
};
