<template>
  <div class="s-select" v-click-outside="onClose">
    <div class="s-select__selection" @click="onOpen">
      <div class="s-select__selection-selected">{{values[selectedIndex]}}</div>
      <div class="s-select__selection-placeholder">{{placeholder}}</div>
      <svg viewBox="0 0 13 8" class="s-select__selection-arrow" xmlns="http://www.w3.org/2000/svg"><g id="select-arrow-Page-1" fill="none" fill-rule="evenodd" opacity=".33"><g id="select-arrow-index_sbc_green" transform="translate(-823 -995)" fill="#4FFFE8"><g id="select-arrow-Group-29" transform="translate(721 966)"><g id="select-arrow-Group-31"><g id="select-arrow-Category"><path d="M106.25 26.75l5.086 5.086a1 1 0 0 1 0 1.414l-5.086 5.086a1 1 0 0 1-1.707-.707V27.457a1 1 0 0 1 1.707-.707z" id="select-arrow-Path-5" transform="rotate(90 108.086 32.543)"/></g></g></g></g></g></svg>
    </div>
    <div class="s-select__dropdown" :class="{'is-open': isOpen}">
      <div
        class="s-select__dropdown-item"
        v-for="(value, index) of values"
        :class="{'is-active': index === selectedIndex}"
        @click="onSelect(index)"
        :key="index"
      >
        {{value}}
      </div>
    </div>
  </div>
</template>


<script>
/**
 * Component: Select (используется для выбора валюты и категории в списке токенов)
 *
 * @version 1.0.0
 */


export default {
  name: 's-select',

  props: {
    /**
     * Индекс выбранного элемента
     */
    selectedIndex: {
      type: Number,
      default: 0,
    },

    placeholder: String,

    /**
     * Варианты выбора
     */
    values: {
      type: Array,
      required: true,
    },
  },

  data() {
    return {
      isOpen: false,
    };
  },

  methods: {
    onOpen() {
      this.isOpen = true;
    },

    onClose() {
      this.isOpen = false;
    },

    onSelect(selectedIndex) {
      this.onClose();
      this.$emit('select', selectedIndex);
    },
  },
};
</script>

<style lang="scss">
.s-select {
  $this: &;
  position: relative;
  z-index: 10;

  &__selection {
    position: relative;
    background: rgba(4,31,32,0.63);
    border-radius: 5px;
    padding: 12px 50px 12px 14px;
    box-sizing: border-box;
    cursor: pointer;
    transition: background-color .15s ease-in-out;

    &:hover {
      background: rgba(4,31,32,1);
    }

    @media only screen and (max-width: "768px") {
      padding: 6px 40px 6px 10px;
    }
  }

  &__selection-selected {
    text-transform: uppercase;
    color: #4FFFE8;
    font-size: 14px;
    font-weight: 600;

    @media only screen and (max-width: "768px") {
      font-size: 11px;
    }
  }

  &__selection-placeholder {
    color: #2A5C5E;
    font-size: 13px;
    line-height: 17px;

    @media only screen and (max-width: "768px") {
      font-size: 11px;
    }
  }

  &__selection-arrow {
    position: absolute;
    top: calc(50% - 6px);
    right: 14px;
    width: 12px;
    height: 7px;
  }

  &__dropdown {
    position: absolute;
    width: 100%;
    bottom: 4px;
    left: -9999px;
    opacity: 0;
    transform: translate(0, calc(100% - 6px));
    max-height: 150px;
    overflow-y: auto;
    border-radius: 0 0 5px 5px;
    background: rgba(4,31,32,1);
    transition: transform .15s ease-in-out, opacity .15s ease-in-out, background-color .15s ease-in-out;

    &.is-open {
      transform: translate(0, 100%);
      opacity: 1;
      left: 0;
    }

    &::-webkit-scrollbar {
      width: 3px;
    }

    &::-webkit-scrollbar-track {
      background: transparent;
    }

    &::-webkit-scrollbar-thumb {
      background: #073639;
    }
  }

  &__dropdown-item {
    padding: 8px 14px;
    font-size: 14px;
    font-weight: 500;
    color: #2A5C5E;
    cursor: pointer;
    transition: all .15s ease-in-out;

    &:hover {
      color: #4FFFE8;
      background: rgba(255,255,255,.03);
    }

    &.is-active {
      color: #4FFFE8;
      cursor: default;
      background: none;

      &:hover {
        background: none;
      }
    }
  }
}
</style>
