import lstore from '@/util/localStorage';
import log from '@/util/logger';
import * as types from '@/store/mutation-types';
import { KEY_ACTIVE_CURRENCY } from '@/util/constants';


/**
 * Action: Загрузка данных о валютах из локал стореджа
 *
 * @since Version 1.0.0
 */
const loadActiveCurrencyFromCash = ({ commit }) => {
  const activeCurrency = lstore.getValue(KEY_ACTIVE_CURRENCY);
  if (activeCurrency) {
    commit(types.SET_ACTIVE_CURRENCY, activeCurrency);
  } else {
    commit(types.SET_ACTIVE_CURRENCY, 'USD');
  }
};


/**
 * Action: Устанавливает активную валюту и вызывает последовательность экшинов необходимых после смены валюты
 *
 * @param {string} activeCurrency - короткое название валюты
 *
 * @since Version 1.0.0
 */
const setActiveCurrency = async ({ state, commit, dispatch }, activeCurrency) => {
  if (Object.prototype.hasOwnProperty.call(state.currencies, activeCurrency)) {
    commit(types.SET_ACTIVE_CURRENCY, activeCurrency);
    await dispatch('globalData/loadGlobalData', undefined, { root: true });
  } else {
    log.error(`There is not such currency: ${activeCurrency}`);
  }
};


export default {
  loadActiveCurrencyFromCash,
  setActiveCurrency,
};
