import Vue from 'vue';
import vClickOutside from 'v-click-outside';
import VueClipboard from 'vue-clipboard2';

// import { APP_VERSION, APP_BUILD_DATE } from '@/config';

import App from './App.vue';
import router from './router';
import store from './store';
import './registerServiceWorker';
import i18n from './i18n';
import { SConstantsPlugin } from './util/plugins';


Vue.config.productionTip = false;

Vue
  .use(VueClipboard)
  .use(vClickOutside)
  .use(SConstantsPlugin);

new Vue({
  router,
  store,
  render: h => h(App),
  i18n,

  created() {
    this.$store.dispatch('localAppInit');
  },

  mounted() {
    // eslint-disable-next-line no-console
    console.log('SBC beta version');
    // console.log(`SBC v${APP_VERSION}(${APP_BUILD_DATE})`);
  },
}).$mount('#app');
