<script>
/**
 * Component: Всплывающее окно с информацией о токене
 *
 * @version 1.0.0
 */


import { mapGetters } from 'vuex';

import openLink from '@/util/openLink';

// Containers
import TheTokenExchange from '@/containers/TheTokenExchange.vue';

export default {
  name: 'the-token-popup',

  props: {
    toggleTokenPopupShow: Function,
    isTokenPopupShow: Boolean,

    /**
     * Код токена для отборажения
     */
    tokenCode: {
      validator: prop => typeof prop === 'string' || prop === null,
      required: true,
    },
  },

  computed: {
    ...mapGetters({
      getTokenData: 'tokens/getTokenData',
    }),

    token() {
      return this.getTokenData(this.tokenCode);
    },
  },

  watch: {
    isTokenPopupShow() {
      if (this.isTokenPopupShow) {
        this.$nextTick().then(() => document.body.classList.add('popup-open'));
      } else {
        this.$nextTick().then(() => document.body.classList.remove('popup-open'));
      }
    },
  },

  methods: {
    /**
     * Открытке линка на вайтперпер
     */
    onOpenWhitePaper() {
      if (this.token && this.token.whitePaper) {
        openLink(this.token.whitePaper);
      }
    },

    /**
     * Открытке линка на сайт
     */
    onOpenwebsite() {
      if (this.token && this.token.website) {
        openLink(this.token.website);
      }
    },
  },

  components: {
    TheTokenExchange,
  },
};
</script>

