/**
 * Getter: Возвращает массив со всеми доступными валютами
 *
 * @returns {array} Возвращает массив коротких названий валют
 */
const currencies = state => state.currencies;


/**
 * Getter: Возвращает активную валюту валютами
 *
 * @returns {string} - Короткое название активной валюты
 */
const activeCurrency = state => state.activeCurrency;


/**
 * Getter: Возвращает активную все данные по активной валюте
 *
 * @returns {object} - Объект с информацией по активной валюте
 */
const activeCurrencyData = state => state.currencies[state.activeCurrency];

export default {
  activeCurrency,
  activeCurrencyData,
  currencies,
};
