import log from '@/util/logger';
import { TOKENS_ICONS_BASE_URL } from '@/config';


/**
 * Getter: Возвращает URL
 *
 * @param {string} currenceShortName - короткое название валюты
 *
 * @returns {(sting|null)} Returns URL of curence logo or null if there is not image for currence
 */
const getTokenIconURL = state => (code) => {
  let res = null;

  if (Object.prototype.hasOwnProperty.call(state.tokensInfo, code) && state.tokensInfo[code].icon) {
    res = `${TOKENS_ICONS_BASE_URL}${state.tokensInfo[code].icon}`;
  } else {
    log.warn(`No icon for this currency: ${code}`, {
      module: 'tokens',
      layer: 'frontend',
    });
  }

  return res;
};

/**
 * Getter: Возвращает объек со всеми доступными валютами
 *
 * @returns {array} Возвращает объек свойствами укоторго являются короткие названия валют
 *
 * @since Vesion 1.0.0
 */
const tokens = state => Object.values(state.tokens).map(item => (Object.prototype.hasOwnProperty.call(state.tokensInfo, item.code)
  ? { ...item, ...state.tokensInfo[item.code] }
  : item)).filter(token => token.send === true);


/**
 * Getter: Возвращает информацию по конкретному токену
 *
 * @since Vesion 1.0.0
 */
const getTokenData = state => (tokenCode) => {
  let res = null;
  if (Object.prototype.hasOwnProperty.call(state.tokens, tokenCode)) {
    res = Object.prototype.hasOwnProperty.call(state.tokensInfo, tokenCode)
      ? { ...state.tokens[tokenCode], ...state.tokensInfo[tokenCode] }
      : state.tokens[tokenCode];
  } else {
    log.error(`There is not such token: ${tokenCode}`);
  }
  return res;
};


/**
 * Getter: Возвращает флаг загружены ли уже все токены
 *
 * @returns {boolean}
 *
 * @since Version 1.0.0
 */
const isTokensLoaded = state => state.isTokensLoaded;

export default {
  getTokenData,
  getTokenIconURL,
  isTokensLoaded,
  tokens,
};
