/**
 * Action: Первичной инициализации приложения
 */
const localAppInit = async ({ dispatch }) => {
  await dispatch('currencies/loadActiveCurrencyFromCash');
  await dispatch('tokens/loadTokensFromCash');
  await dispatch('globalData/loadGlobalData');
};

export default {
  localAppInit,
};
