<script>
// Containers
import TheInput from '@/containers/TheInput.vue';
import TheSwitchCheckbox from '@/containers/TheSwitchCheckbox.vue';

export default {
  name: 'the-sent-token-popup',

  props: {
    onClose: Function,
  },

  data() {
    return {
      step: 1,
      isCopied: false,
      isPrivacy: false,
      isTerms: false,
    };
  },

  methods: {
    changeStep(step) {
      this.step = step;
    },

    onWalletCopy() {
      if (!this.isCopied) {
        this.isCopied = true;

        setTimeout(() => { this.isCopied = false; }, 4000);
      }
    },

    prevent(event) {
      event.preventDefault();
      event.stopPropagation();
    },

    onPrivacyOpen(event) {
      event.preventDefault();
      event.stopPropagation();
      this.isPrivacy = true;
    },

    onPrivacyClose() {
      this.isPrivacy = false;
    },

    onTermsOpen(event) {
      event.preventDefault();
      event.stopPropagation();
      this.isTerms = true;
    },

    onTermsClose() {
      this.isTerms = false;
    },
  },

  components: {
    TheInput,
    TheSwitchCheckbox,
  },
};
</script>

