/**
 * Функция хелпер, которая позволяет генерировать названия мутаций для асинхронных экшинов
 *
 * @param {string} type - Строка, на основании которой будут сгенерированны названия мутаций
 */
const createAsyncMutation = type => ({
  SUCCESS: `${type}_SUCCESS`,
  FAILURE: `${type}_FAILURE`,
  PENDING: `${type}_PENDING`,
  // loadingKey: `${type}_PENDING`,
  // stateKey: `${type}_DATA`,
});

// UI
export const SET_LANGUAGE = 'SET_LANGUAGE';


// Tokens
export const GET_TOKENS_ASYNC = createAsyncMutation('GET_TOKENS');
export const SET_TOKENS = 'SET_TOKENS';
export const SET_ACTIVE_TOKEN = 'SET_ACTIVE_TOKEN';

// Currencies
export const SET_ACTIVE_CURRENCY = 'SET_ACTIVE_CURRENCY';

// Global data
export const SET_BITCOIN_PERSENTAGE_OF_MARKET_CAP = 'SET_BITCOIN_PERSENTAGE_OF_MARKET_CAP';
export const SET_TOTAL_MARKET_CAP = 'SET_TOTAL_MARKET_CAP';
export const SET_TOTAL_VOLUME_24H = 'SET_TOTAL_VOLUME_24H';
export const GET_GLOBAL_DATA_ASYNC = createAsyncMutation('GET_GLOBAL_DATA');
