/**
 * @module store/globalData/mutations
 *
 * @version 1.0.0
 * @copyright SBC 2018
 */


import log from '@/util/logger';
import * as types from '@/store/mutation-types';

/* eslint-disable no-param-reassign */
export default {
  /**
   * Mutation: Задание значения bitcoinPercentageOfMarketCap
   *
   * @since Version 1.0.0
   */
  [types.SET_BITCOIN_PERSENTAGE_OF_MARKET_CAP](state, bitcoinPercentageOfMarketCap) {
    state.bitcoinPercentageOfMarketCap = bitcoinPercentageOfMarketCap;
  },


  /**
   * Mutation: Задание значения totalMarketCap
   *
   * @since Version 1.0.0
   */
  [types.SET_TOTAL_MARKET_CAP](state, totalMarketCap) {
    state.totalMarketCap = totalMarketCap;
  },


  /**
   * Mutation: Задание значения totalVolume24h
   *
   * @since Version 1.0.0
   */
  [types.SET_TOTAL_VOLUME_24H](state, totalVolume24h) {
    state.totalVolume24h = totalVolume24h;
  },


  /**
   * Mutation: Начало асинхронной загрузке списка валют
   *
   * @since Version 1.0.0
   */
  [types.GET_GLOBAL_DATA_ASYNC.PENDING](state) {
    state.isGlobalDataLoading = true;
  },

  /**
   * Mutation: Обработка ошибки при асинхронной загрузке списка валют
   *
   * @since Version 1.0.0
   */
  [types.GET_GLOBAL_DATA_ASYNC.FAILURE](state, error) {
    state.isGlobalDataLoading = false;
    log.error(`https://coinmarketcap.com global data getting error: ${error}`, {
      module: 'globalData',
      layer: 'coinmarketcap.com',
    });
  },

  /**
   * Mutation: Успешная асинхронной загрузке списка валют
   *
   * @since Version 1.0.0
   */
  [types.GET_GLOBAL_DATA_ASYNC.SUCCESS](state, { globalData, activeCurrency }) {
    if (Object.prototype.hasOwnProperty.call(globalData.quotes, activeCurrency)) {
      state.bitcoinPercentageOfMarketCap = globalData.bitcoin_percentage_of_market_cap;
      state.totalMarketCap = globalData.quotes[activeCurrency].total_market_cap;
      state.totalVolume24h = globalData.quotes[activeCurrency].total_volume_24h;
      state.isGlobalDataLoading = false;
    } else {
      throw new Error(`Active currency ${activeCurrency} is not equal to the currency in the backend data`);
    }
  },
};
/* eslint-enable no-param-reassign */
