import lstore from '@/util/localStorage';

import * as types from '@/store/mutation-types';
import { KEY_ACTIVE_CURRENCY } from '@/util/constants';

/* eslint-disable no-param-reassign */
export default {
  /**
   * Mutation: Загрузка токенов из локал стореджа
   *
   * @param {string} currency - Короткое название валюты
   *
   * @since Version 1.0.0
   */
  [types.SET_ACTIVE_CURRENCY](state, currency) {
    state.activeCurrency = currency;
    lstore.setValue(KEY_ACTIVE_CURRENCY, currency);
  },
};
/* eslint-enable no-param-reassign */
