// Components
import EBigNumber from '@/components/EBigNumber.vue';

// Containers
import TheOrderForm from '@/containers/TheOrderForm.vue';


export default {
  name: 'the-header',

  data() {
    return {
      isMenuOpen: false,
      isLanguageSelect: false,
      language: 'English',
    };
  },

  computed: {
    ...mapGetters({
      bitcoinPercentageOfMarketCap: 'globalData/bitcoinPercentageOfMarketCap',
      totalMarketCap: 'globalData/totalMarketCap',
      totalVolume24h: 'globalData/totalVolume24h',
      activeCurrencyData: 'currencies/activeCurrencyData',
    }),
  },

  methods: {
    toggleMenu() {
      this.isMenuOpen = !this.isMenuOpen;
    },

    toggleLanguage() {
      this.isLanguageSelect = !this.isLanguageSelect;
    },

    closeLanguage() {
      this.isLanguageSelect = false;
    },

    selectLanguage(language) {
      this.language = language;
      this.closeLanguage();
    },
  },

  mounted() {
    // this.$nextTick(() => {
