        <div class="the-order-form__field">
          <s-select-token label="You're sending" @input="onInputSaleToken"/>
        </div>

        <div class="the-order-form__mobile-rates">
          <span class="the-order-form__mobile-rates-pale">Swap Rate:</span> 1 {{saleToken}} = 98 412.42 452 {{buyToken}}
        </div>

        <div class="the-order-form__field">
          <s-select-token label="You'll get" @input="onInputBuyToken"/>
        </div>

        <div class="the-order-form__ven">
          <input class="the-order-form__ven-input" id="ven" />
          <label for="ven" class="the-order-form__label">Your {{buyToken}} wallet address</label>
        </div>
        <div class="the-order-form__refund">
          + Add a refund address for refund cases.
          <span class="the-order-form__refund-label">Optional</span>
        </div>

        <div class="the-order-form__submit-wrapper">
          <div class="the-order-form__rates">
            <div class="the-order-form__rate-item">
              <div class="the-order-form__rate-label">Swap Rate</div>
              <div class="the-order-form__rate-text">1 {{saleToken}} = 9842.5215 {{buyToken}}</div>
            </div>
            <div class="the-order-form__rate-item">
              <div class="the-order-form__rate-label">Min. - Max. Order</div>
              <div class="the-order-form__rate-text"><e-big-number :value="min"/> {{saleToken}} — <e-big-number :value="max"/> {{saleToken}}</div>
            </div>
          </div>

          <div class="the-order-form__swap">
            <button class="the-order-form__swap-button" @click="toStep1">
              <div class="the-order-form__swap-button-content">
                Swap

                <div class="the-order-form__swap-button-content-icons">
                  <token-icon class="the-order-form__swap-button-content-icon" :tokenCode="saleToken" />

                  <svg viewBox="0 0 14 16" xmlns="http://www.w3.org/2000/svg" class="the-order-form__swap-button-content-to"><g stroke="#00FEB8" stroke-width="1.5" fill="none" fill-rule="evenodd" stroke-linecap="round"><path d="M6.28 1L13 7.72l-6.72 6.72"/><path d="M1 1l6.72 6.72L1 14.44"/></g></svg>
                  <token-icon class="the-order-form__swap-button-content-icon" :tokenCode="buyToken" tokenCode="ETH"/>
                </div>
              </div>
            </button>
          </div>
        </div>
      </div>

      <div class="the-order-form__send" :class="{isActive: formShow === 'step1'}">
        <div class="the-order-form__heading">Swap your crypto</div>
        <TheStep1 v-if="formShow === 'step1'" :back="toCalculator" />
      </div>
  </div>
