<script>
/**
 * Container: Форма создания ордера в заголовке главной страницы
 *
 * @version 1.0.0
 */

import { BigNumber } from 'bignumber.js';
import { mapGetters } from 'vuex';

import { getExchangeLimits } from '@/api/';

// Components
import EBigNumber from '@/components/EBigNumber.vue';
import TokenIcon from '@/components/TokenIcon.vue';
import SSelectToken from '@/components/SSelectToken.vue';

// Containers
import TheStep1 from '@/containers/TheStep1.vue';


export default {
  name: 'the-order-form',

  data() {
    return {
      formShow: 'calculator',
      saleToken: 'BTC',
      buyToken: 'ETH',
      min: null,
      max: null,
    };
  },

  computed: {
    /**
     * Флаг, который пказывает загружены ли все необходимые для отображения компонента данные
     */
    isDataLoaded() {
      return this.isTokensLoaded;
    },

    saleTokenData() {
      return this.getTokenData(this.saleToken);
    },

    ...mapGetters({
      isTokensLoaded: 'tokens/isTokensLoaded',
      getTokenData: 'tokens/getTokenData',
    }),
  },

  watch: {
    async saleToken() {
      await this.getMinMax();
    },

    async buyToken() {
      await this.getMinMax();
    },
  },

  async created() {
    await this.getMinMax();
  },

  methods: {
    toCalculator() {
      this.formShow = 'calculator';
    },

    toStep1() {
      this.formShow = 'step1';
    },

    onInputSaleToken(value) {
      this.saleToken = value.token;
    },

    onInputBuyToken(value) {
      this.buyToken = value.token;
    },

    async getMinMax() {
      if (this.saleToken && this.buyToken) {
        const { min, max } = await getExchangeLimits(this.saleToken, this.buyToken);
        this.min = BigNumber(min);
        this.max = BigNumber(max);
      }
    },
  },

  components: {
    EBigNumber,
    SSelectToken,
    TheStep1,
    TokenIcon,
  },
};
</script>

