<template>
  <div class="token-icon" v-if="tokenImgURL">
    <div class="token-icon__bg">
      <img v-if="isIncreasing" :src="iconIncreasing" alt="">
      <img v-else :src="iconDecreasing" alt="">
    </div>
    <img
      :src="tokenImgURL"
      :alt="tokenCode"
      class="token-icon__img"
    >

  </div>
</template>


<script>
/**
 * Component: Компонент отображения иконки токена
 *
 * @todo Рализовать инлайн для фонов иконок
 *
 * @version 1.0.0
 */
import { mapGetters } from 'vuex';

// Icons
import iconIncreasing from '@/assets/images/coin_green_bg.svg';
import iconDecreasing from '@/assets/images/coin_red_bg.svg';

export default {
  name: 'token-icon',

  data() {
    return {
      // Icons
      iconIncreasing,
      iconDecreasing,
    };
  },

  props: {
    /**
     * Короткое название токена
     */
    tokenCode: {
      type: String,
      required: true,
    },

    /**
     * increasing
     */
    isIncreasing: {
      type: Boolean,
      default: true,
    },
  },

  computed: {
    tokenImgURL() {
      return this.getTokenIconURL(this.tokenCode);
    },

    ...mapGetters({
      getTokenIconURL: 'tokens/getTokenIconURL',
    }),
  },
};
</script>

<style lang="scss">
.token-icon {
  $this: &;
  position: relative;
  width: 30px;
  height: 30px;

  svg {
    width: 30px;
    height: 30px;
  }

  &__img {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
  }

  &__bg {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
  }
}
</style>
