<template>
  <div
    class="s-select-token"
    :class="{ 'loading': isLoading }"
  >
    <div class="s-select-token__selection">
      <div v-if="isLoading" class="s-select-token__loading" />

      <div class="s-select-token__selection-box" v-if="!isLoading">
        <input
          :id="id"
          v-model="amount"
          ref="amount"
          type="text"
          class="s-select-token__amount-input"
        />
        <label :for="id" class="s-select-token__label">{{label}}</label>

        <div class="s-select-token__selection-box-token" @click="openSearch">
          <div class="s-select-token__selection-box-name">
            {{ selectedCurrency.code }}
          </div>
          <div class="s-select-token__selection-box-icon">
            <TokenIcon :tokenCode="selectedCurrency.code" />
          </div>
        </div>
      </div>

      <div class="s-select-token__search" :class="[!isSearchOpen ? 'open' : 'close']" v-if="isSearchOpen" v-click-outside="hideSearch">

        <div class="s-select-token__search-input">
          <div class="s-select-token__search-input-icon">
            <svg viewBox="0 0 15 15" xmlns="http://www.w3.org/2000/svg"><g id="Desktop" fill="none" fill-rule="evenodd"><g id="search-icon" transform="translate(-826 -234)" fill="#00FEB8"><g id="Group-5" transform="translate(-172 -38)"><g id="Group-34" transform="translate(299 81)"><g id="Group-15" transform="translate(679 169)"><path d="M30.385 28.346a4.043 4.043 0 0 0-4.039-4.038 4.043 4.043 0 0 0-4.038 4.038 4.043 4.043 0 0 0 4.038 4.039 4.043 4.043 0 0 0 4.039-4.039zm4.615 7.5c0 .631-.523 1.154-1.154 1.154-.306 0-.604-.126-.811-.343l-3.092-3.082A6.344 6.344 0 0 1 20 28.347 6.344 6.344 0 0 1 26.346 22a6.344 6.344 0 0 1 5.229 9.943l3.091 3.092c.208.207.334.505.334.811z" id="search-icon-path"/></g></g></g></g></g></svg>
          </div>

          <input
            v-model="searchInput"
            ref="searchInput"
            type="text" placeholder="Type token short or long name..." class="s-select-token__search-input-input">
        </div>

        <div class="s-select-token__search-body">

          <div class="s-select-token__search-body-label">
            Popular tokens
          </div>
          <div class="s-select-token__search-body-group">
            <div
              v-for="({ code, name }, index) of filteredtokens"
              class="s-select-token__search-body-item"
              :class="{active: code === selectedCurrency.code}"
              @click="selectCurrency(code, name)"
              :key="index"
            >
              <div class="s-select-token__search-body-item-icon">
                <token-icon :tokenCode="code" />
              </div>
              <div class="s-select-token__search-body-item-fullname">
                {{ name }}
              </div>


              <div class="s-select-token__search-body-item-shortname">
                {{ code }}
              </div>
            </div>
          </div>

          <!--<div class="s-select-token__search-body-label">-->
            <!--All tokens-->
          <!--</div>-->
        </div>
      </div>
    </div>
  </div>

</template>


<script>
import { mapGetters } from 'vuex';

// Components
import TokenIcon from '@/components/TokenIcon.vue';

export default {
  name: 's-select-token',

  props: {
    label: {
      type: String,
    },

    id: {
      type: String,
    },

    tokenCode: {
      type: String,
    },
  },

  data() {
    return {
      isSearchOpen: false,
      selectedCurrency: null,
      searchInput: '',
      amount: '',
    };
  },

  async created() {
    this.tokens = this.tokensFromStore;
    this.selectedCurrency = this.tokens[3];
  },

  computed: {
    filteredtokens() {
      const searchInput = this.searchInput.toLowerCase();
      return this.searchInput && this.tokens && this.tokens.length > 0
        ? this.tokens.filter(c => c.name.toLowerCase().includes(searchInput) || c.code.toLowerCase().includes(searchInput))
        : this.tokens;
    },

    isLoading() {
      return !this.isTokensLoaded;
    },

    ...mapGetters({
      isTokensLoaded: 'tokens/isTokensLoaded',
      tokensFromStore: 'tokens/tokens',
    }),
  },

  methods: {
    openSearch() {
      this.isSearchOpen = true;
      this.$nextTick(() => this.$refs.searchInput.focus());
    },

    hideSearch() {
      this.isSearchOpen = false;
      this.searchInput = '';
    },

    selectCurrency(code, name) {
      this.selectedCurrency = { code, name };
      this.hideSearch();
    },

    /**
     * Генерация события при изменении данных пользователем
     */
    emitEvent() {
      this.$emit('input', {
        amount: this.amount,
        token: this.selectedCurrency.code,
      });
    },
  },

  watch: {
    /**
     * Отслеживание изменения выбранного токена
     */
    selectedCurrency() {
      this.emitEvent();
    },


    /**
     * Отслеживание изменения количества токена
     */
    amount() {
      this.emitEvent();
    },
  },

  components: {
    TokenIcon,
  },
};
</script>

